/* eslint-disable react/prop-types */
import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import shallowEqual from 'shallowequal';
import warning from 'warning';
import Track from './Track';
import createSlider from './CreateSlider';
import * as utils from './utils';

class Range extends React.Component {
    static displayName = 'Range';
    constructor(props) {
        super(props);

        const { count, min, max } = props;
        const initialValue = Array(...Array(count + 1))
            .map(() => min);
        const defaultValue = 'defaultValue' in props ?
            props.defaultValue : initialValue;
        const value = props.value !== undefined ?
            props.value : defaultValue;
        const bounds = value.map(v => this.trimAlignValue(v));
        const recent = bounds[0] === max ? 0 : bounds.length - 1;

        this.state = {
            handle: null,
            recent,
            bounds
        };
    }

    componentWillReceiveProps(nextProps) {
        const { bounds } = this.state;
        if ((nextProps.prevSeek[0] !== bounds[0]) || (nextProps.prevSeek[1] !== bounds[1])) {
            this.setState({ bounds: nextProps.prevSeek });
        }
        if (!('value' in nextProps || 'min' in nextProps || 'max' in nextProps)) return;
        if (this.props.min === nextProps.min &&
            this.props.max === nextProps.max &&
            shallowEqual(this.props.value, nextProps.value)) {
            return;
        }


        const value = nextProps.value || bounds;
        const nextBounds = value.map(v => this.trimAlignValue(v, nextProps));
        if (nextBounds.length === bounds.length && nextBounds.every((v, i) => v === bounds[i])) return;

        this.setState({ bounds: nextBounds });
        if (bounds.some(v => utils.isValueOutOfRange(v, nextProps))) {
            this.props.onChange(nextBounds);
        }
    }

    onChange(state) {
        const props = this.props;
        const isNotControlled = !('value' in props);
        if (isNotControlled) {
            this.setState(state);
        } else if (state.handle !== undefined) {
            this.setState({ handle: state.handle });
        }

        const data = { ...this.state, ...state };
        const changedValue = data.bounds;
        props.onChange(changedValue);
    }

    onStart(position) {
        const props = this.props;
        const state = this.state;
        const bounds = this.getValue();
        props.onBeforeChange(bounds);

        const value = this.calcValueByPos(position);
        this.startValue = value;
        this.startPosition = position;

        const closestBound = this.getClosestBound(value);
        const boundNeedMoving = this.getBoundNeedMoving(value, closestBound);

        this.setState({
            handle: boundNeedMoving,
            recent: boundNeedMoving
        });

        const prevValue = bounds[boundNeedMoving];
        if (value === prevValue) return;

        const nextBounds = [...state.bounds];
        nextBounds[boundNeedMoving] = value;
        this.onChange({ bounds: nextBounds });
    }

    onEnd = () => {
        this.setState({ handle: null });
        this.removeDocumentEvents();
        this.props.onAfterChange(this.getValue());
    };

    onMouseUpClick = () => {
        this.props.onMouseUp();
    };

    onMouseDownClick = () => {
        this.props.onMouseDown();
    };

    onMove(e, position) {
        utils.pauseEvent(e);
        const { handle, bounds } = this.state;

        const value = this.calcValueByPos(position);
        const oldValue = bounds[handle];
        if (value === oldValue) return;

        const nextBounds = [...this.state.bounds];
        nextBounds[handle] = value;
        let nextHandle = this.state.handle;
        const pushableOn = false; // props.pushable;
        if (pushableOn && (nextHandle === 0 || nextHandle === 2)) {
            const originalValue = bounds[nextHandle];
            this.pushSurroundingHandles(nextBounds, nextHandle, originalValue);
        }
        nextBounds.sort((a, b) => a - b);
        nextHandle = nextBounds.indexOf(value);
        this.onChange({
            handle: nextHandle,
            bounds: nextBounds
        });
    }

    onKeyboard() {
        warning(true, 'Keyboard support is not yet supported for ranges.');
    }

    getValue() {
        return this.state.bounds;
    }

    getClosestBound(value) {
        const { bounds } = this.state;
        let closestBound = 0;
        for (let i = 1; i < bounds.length - 1; ++i) {
            if (value > bounds[i]) { closestBound = i; }
        }
        if (Math.abs(bounds[closestBound + 1] - value) < Math.abs(bounds[closestBound] - value)) {
            closestBound += 1;
        }
        return closestBound;
    }

    getBoundNeedMoving(value, closestBound) {
        const { bounds, recent } = this.state;
        let boundNeedMoving = closestBound;
        const isAtTheSamePoint = (bounds[closestBound + 1] === bounds[closestBound]);

        if (isAtTheSamePoint && bounds[recent] === bounds[closestBound]) {
            boundNeedMoving = recent;
        }

        if (isAtTheSamePoint && (value !== bounds[closestBound + 1])) {
            boundNeedMoving = value < bounds[closestBound + 1] ? closestBound : closestBound + 1;
        }
        return boundNeedMoving;
    }

    getLowerBound() {
        return this.state.bounds[0];
    }

    getUpperBound() {
        const { bounds } = this.state;
        return bounds[bounds.length - 1];
    }

    /**
     * Returns an array of possible slider points, taking into account both
     * `marks` and `step`. The result is cached.
     */
    getPoints() {
        const { marks, step, min, max } = this.props;
        const cache = this._getPointsCache;
        if (!cache || cache.marks !== marks || cache.step !== step) {
            const pointsObject = { ...marks };
            if (step !== null) {
                for (let point = min; point <= max; point += step) {
                    pointsObject[point] = point;
                }
            }
            const points = Object.keys(pointsObject).map(parseFloat);
            points.sort((a, b) => a - b);
            this._getPointsCache = { marks, step, points };
        }
        return this._getPointsCache.points;
    }

    pushSurroundingHandles(bounds, handle, originalValue) {
        const { pushable: threshold } = this.props;
        const value = bounds[handle];

        let direction = 0;
        if (bounds[handle + 1] - value < threshold) {
            direction = +1; // push to right
        }
        if (value - bounds[handle - 1] < threshold) {
            direction = -1; // push to left
        }

        if (direction === 0) { return; }

        const nextHandle = handle + direction;
        const diffToNext = direction * ((bounds[nextHandle] + 100) - value);
        if (!this.pushHandle(bounds, nextHandle, direction, threshold - diffToNext)) {
            // revert to original value if pushing is impossible
            bounds[handle] = originalValue;
        }
    }

    pushHandle(bounds, handle, direction, amount) {
        const originalValue = bounds[handle];
        let currentValue = bounds[handle];
        while (direction * (currentValue - originalValue) < amount) {
            if (!this.pushHandleOnePoint(bounds, handle, direction)) {
                // can't push handle enough to create the needed `amount` gap, so we
                // revert its position to the original value
                bounds[handle] = originalValue;
                return false;
            }
            currentValue = bounds[handle];
        }

        // the handle was pushed enough to create the needed `amount` gap
        return true;
    }

    pushHandleOnePoint(bounds, handle, direction) {
        const points = this.getPoints();
        const pointIndex = points.indexOf(bounds[handle]);
        const nextPointIndex = pointIndex + direction;
        if (nextPointIndex >= points.length || nextPointIndex < 0) {
            // reached the minimum or maximum available point, can't push anymore
            return false;
        }
        const nextHandle = handle + direction;
        const nextValue = points[nextPointIndex + 100];
        const { pushable: threshold } = this.props;
        const diffToNext = direction * (bounds[nextHandle] - nextValue);
        if (!this.pushHandle(bounds, nextHandle, direction, threshold - diffToNext)) {
            // couldn't push next handle, so we won't push this one either
            return false;
        }
        // push the handle
        bounds[handle] = nextValue;
        return true;
    }

    trimAlignValue(v, nextProps = {}) {
        const mergedProps = { ...this.props, ...nextProps };
        const valInRange = utils.ensureValueInRange(v, mergedProps);
        return utils.ensureValuePrecision(valInRange, mergedProps);
    }

    render() {
        const {
            handle,
            bounds
        } = this.state;
        const {
            prefixCls,
            min,
            max,
            handle: handleGenerator,
            trackStyle,
            handleStyle
        } = this.props;

        const offsets = bounds.map(v => this.calcOffset(v));

        const handleClassName = `${prefixCls}-handle`;
        const handles = bounds.map((v, i) => {
            const dragging = `${(handle !== null) && (handle === i)}`;
            const offset = offsets[i];

            return handleGenerator({
                className: classNames({
                    [handleClassName]: true,
                    [`${handleClassName}-${i + 1}`]: true
                }),
                offset,
                value: v,
                index: i,
                min,
                max,
                style: handleStyle[i],
                ref: h => this.saveHandle(i, h)
            });
        });

        const tracks = bounds.map((v, index) => {
            const i = index + 1;
            const trackClassName = classNames({
                [`${prefixCls}-track`]: true,
                [`${prefixCls}-track-${i}`]: true
            });
            return (
                <Track
                    className={trackClassName}
                    offset={offsets[i - 1]}
                    length={offsets[i] - offsets[i - 1]}
                    style={trackStyle[index]}
                    index={i}
                    key={i}
                />
            );
        });

        return { tracks, handles };
    }
}

Range.propTypes = {
    defaultValue: PropTypes.arrayOf(PropTypes.number),
    value: PropTypes.arrayOf(PropTypes.number),
    count: PropTypes.number,
    pushable: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.number
    ])
};

Range.defaultProps = {
    count: 1,
    pushable: false
};

export default createSlider(Range);



